import {
  BuyButton,
  CakeCard,
  CakeImage,
  CakeName,
  CakePrice,
  CakesList,
  CategoryContainer,
  CategoryFilterBar,
  CategoryFilterItem,
  CategorySection,
  CategoryTitle,
  WrapCakeInfo,
  CategoryFilterLink,
} from "./ProductMenuPage.styled";
import { products } from "../../data/data";
import { useParams } from "react-router-dom";
import { useEffect, useMemo } from "react";
const CATEGORY_TITLES = {
  cakes: "",
  cupcakes: "",
  macarons: "",
  tarts: "",
};
export const ProductMenuPage = () => {
  const { category } = useParams();

  const name = CATEGORY_TITLES[category] || " ";

useEffect(() => {
  window.scrollTo({ top: 0, behavior: "smooth" });
}, [category]);

  const filteredProducts = useMemo(
    () =>
      category === "allProducts"
        ? products
        : products.filter((product) => product.type === category),
    [category]
  );

  return (
    <>
      <CategorySection>
        <CategoryContainer>
          <CategoryTitle
            initial={{ opacity: 0, y: 40 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            viewport={{ once: true, amount: 0.2 }}
          >
            {name}
          </CategoryTitle>
          <CategoryFilterBar>
            <CategoryFilterItem active={category === "allProducts" ? "true" : "false"}>
              <CategoryFilterLink to="/menu/allProducts"> 
                
              </CategoryFilterLink>
            </CategoryFilterItem>
            <CategoryFilterItem active={category === "cakes" ? "true" : "false"}>
              <CategoryFilterLink to="/menu/cakes" ></CategoryFilterLink>
            </CategoryFilterItem>
            <CategoryFilterItem active={category === "cupcakes" ? "true" : "false"}>
              <CategoryFilterLink to="/menu/cupcakes">
                
              </CategoryFilterLink>
            </CategoryFilterItem>
            <CategoryFilterItem active={category === "macarons" ? "true" : "false"}>
              <CategoryFilterLink to="/menu/macarons" >
                
              </CategoryFilterLink>
            </CategoryFilterItem>
            <CategoryFilterItem active={category === "tarts" ? "true" : "false"}>
              <CategoryFilterLink to="/menu/tarts" ></CategoryFilterLink>
            </CategoryFilterItem>
          </CategoryFilterBar>
          <CakesList>
            {filteredProducts.map((product) => (
              <CakeCard
                key={product.id}
              >
                <CakeImage src={product.image} />
                <WrapCakeInfo>
                  <CakeName>{product.title}</CakeName>
                  <CakePrice>{product.price} </CakePrice>
                  <BuyButton>  </BuyButton>
                </WrapCakeInfo>
              </CakeCard>
            ))}
          </CakesList>
        </CategoryContainer>
      </CategorySection>
    </>
  );
};
